import swh from '@/assets/sw_helpers.js'

class StellariumController {
  constructor (stelInstance, store) {
    this.stel = stelInstance
    this.store = store
    this._lastSelectionId = null
    this._observerTimer = null
  }

  async selectAndTrackObjectByName (name, zoomFovDeg = 10) {
    try {
      const source = await swh.lookupSkySourceByName(name)
      if (!source) {
        console.warn(`[StellariumController] SkySource not found for: ${name}`)
        return
      }

      let sweObj = swh.skySource2SweObj(source)
      if (!sweObj) {
        sweObj = this.stel.createObj(source.model, source)
      }

      if (!sweObj) {
        console.warn(`[StellariumController] Cannot create/find SWEObject for ${name}`)
        return
      }
      this.stel.core.selection = sweObj
      this.stel.pointAndLock(sweObj, zoomFovDeg * Math.PI / 180)

      console.log('sweObj', sweObj)
      console.log('source', source)
      if (window.AndroidBridge?.postMessage) {
        window.AndroidBridge.postMessage(JSON.stringify({
          type: 'object_selected_by_name',
          payload: {
            name: source.names?.[1] || source.names?.[0] || name,
            types: source.types || [],
            hip: source.hip || null,
            radec: source.radec || null
          }
        }))
      }

      console.log(`[StellariumController]  '${name}' selected and locked`)
    } catch (err) {
      console.error('[StellariumController] selectAndTrackObjectByName error:', err)
    }
  }

  //   
  toggleConstellations (visible) {
    this.stel.core.constellations.lines_visible = visible
  }

  //    (J2000)
  toggleEquatorialGrid (visible) {
    this.stel.core.lines.equatorial.visible = visible
  }

  //    (JNow)
  toggleEquatorialJNowGrid (visible) {
    this.stel.core.lines.equatorial_jnow.visible = visible
  }

  //   
  toggleAzimuthalGrid (visible) {
    this.stel.core.lines.azimuthal.visible = visible
  }

  //   / 
  toggleAtmosphere (visible) {
    this.stel.core.atmosphere.visible = visible
  }

  toggleLandscape (visible) {
    this.stel.core.landscapes.visible = visible
  }

  //   (DSOs)
  toggleDSOs (visible) {
    this.stel.core.dsos.visible = visible
  }

  //   
  setTime (date) {
    this.stel.core.observer.utc = date.getMJD()
  }

  //   
  setLocation (lat, lng, alt = 0) {
    this.stel.core.observer.latitude = lat * Math.PI / 180
    this.stel.core.observer.longitude = lng * Math.PI / 180
    this.stel.core.observer.elevation = alt
  }

  //    ( /)
  setFov (deg) {
    this.stel.core.fov = Math.min(deg * Math.PI / 180, 180)
  }

  //   
  setViewDirection (yawDeg, pitchDeg) {
    if (!this.stel || !this.stel.core) {
      return console.warn('[]   .')
    }
    this.stel.core.observer.yaw = yawDeg * Math.PI / 180
    this.stel.core.observer.pitch = pitchDeg * Math.PI / 180
  }

  moveToRaDec (raDeg, decDeg, fovDeg = 20) {
    // temp   
    const temp = this.stel.createObj('star', {
      model: 'star',
      model_data: {
        ra: raDeg,
        de: decDeg,
        Vmag: 1.0
      },
      names: ['TempTarget'],
      types: ['*']
    })

    this.stel.pointAndLock(temp, fovDeg * Math.PI / 180)
  }

  pointMoveToRaDec (raDeg, decDeg, fovDeg = 20) {
    const temp = this.stel.createObj('star', {
      model: 'star',
      model_data: {
        ra: raDeg,
        de: decDeg,
        Vmag: 1.0
      },
      names: ['TempTarget'],
      types: ['*']
    })

    this.stel.pointAndLock(temp, fovDeg * Math.PI / 180)
    this.stel.core.selection = temp
  }

  setEducationMode () {
    // UI  
    this.store.commit('setValue', { varName: 'barRight', newValue: false })
    this.store.commit('setValue', { varName: 'showMainToolBar', newValue: false })
    this.store.commit('setValue', { varName: 'showSidePanel', newValue: false })
    this.store.commit('setValue', { varName: 'showTimeButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showLocationButton', newValue: false })
    this.store.commit('setValue', { varName: 'showObservingPanelTabsButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showSelectedInfoButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showFPS', newValue: false })
    this.store.commit('setValue', { varName: 'showConstellationsLinesButton', newValue: false })
    this.store.commit('setValue', { varName: 'showConstellationsArtButton', newValue: false })
    this.store.commit('setValue', { varName: 'showAzimuthalGridButton', newValue: false })
    this.store.commit('setValue', { varName: 'showEquatorialGridButton', newValue: false })
    this.store.commit('setValue', { varName: 'showDSOsButton', newValue: false })
    this.store.commit('setValue', { varName: 'showAtmosphereButton', newValue: false })
    this.store.commit('setValue', { varName: 'showEyeTrackingButton', newValue: false })
    this.store.commit('setValue', { varName: 'showSelectedObjectInfo', newValue: false })
  }

  setEducationRightBarMode () {
    // UI  
    this.store.commit('setValue', { varName: 'barRight', newValue: true })
    this.store.commit('setValue', { varName: 'showMainToolBar', newValue: false })
    this.store.commit('setValue', { varName: 'showSidePanel', newValue: false })
    this.store.commit('setValue', { varName: 'showTimeButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showLocationButton', newValue: false })
    this.store.commit('setValue', { varName: 'showObservingPanelTabsButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showSelectedInfoButtons', newValue: false })
    this.store.commit('setValue', { varName: 'showFPS', newValue: false })
    this.store.commit('setValue', { varName: 'showConstellationsLinesButton', newValue: true })
    this.store.commit('setValue', { varName: 'showConstellationsArtButton', newValue: true })
    this.store.commit('setValue', { varName: 'showAzimuthalGridButton', newValue: true })
    this.store.commit('setValue', { varName: 'showEquatorialGridButton', newValue: true })
    this.store.commit('setValue', { varName: 'showDSOsButton', newValue: false })
    this.store.commit('setValue', { varName: 'showAtmosphereButton', newValue: true })
    this.store.commit('setValue', { varName: 'showEyeTrackingButton', newValue: true })
    this.store.commit('setValue', { varName: 'showSelectedObjectInfo', newValue: false })
  }

  async Zoom (targetDeg, duration = 1000) {
    const stel = this.stel
    if (!stel || !stel.core) return

    const start = stel.core.fov
    const target = targetDeg * Math.PI / 180
    const diff = target - start

    const startTime = performance.now()

    return new Promise(resolve => {
      const animate = (now) => {
        const elapsed = now - startTime
        const t = Math.min(1, elapsed / duration)
        stel.core.fov = start + diff * t

        if (t < 1) {
          requestAnimationFrame(animate)
        } else {
          stel.core.fov = target
          resolve()
        }
      }

      requestAnimationFrame(animate)
    })
  }
}

export default StellariumController
